@use 'base/var' as v;
@use 'base/mixins' as m;


.contact{
	display: flex;
	align-items: center;
    justify-content: center;
    margin: 5rem 0;
    padding: 0 2rem;
}
.social{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	background-color: v.$secondary-color;
	box-shadow: v.$shadow;

    a{
        padding: 2rem;
        color:v.$white;
        font-size:1.1rem;
        

        &:hover{
            color:v.$select-box-bg-color;
            transition: all ease 0.3s;
        }
    }

    
}

.contact-box{
	background-color:v.$select-box-bg-color;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 3rem;
	box-shadow: v.$shadow;

    p{
        font-family: v.$AppleSystem;
        color:v.$white-80;
        font-size: 1.4rem;
        font-weight: lighter;
        margin: 0;
        padding: 0;
        text-align: center;
    }
}


.form{
	margin: 1.5rem 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

    input{
        width:25rem;
        height: 4.5rem;
        background-color: transparent;
        border: none;
        border-bottom: 1px solid v.$border-color-1;
        outline: none;
        margin: 1rem 0;
        padding: 1rem;
        box-sizing: border-box;
        color: v.$white-80;
        display: flex;

        &:focus{
            border-bottom: 1px solid v.$white-80;
            transition: all ease 0.5s;
        }
    }

    textarea{
        width:25rem !important;
        height: 20rem !important;
        outline: none;
        background-color: transparent;
        border:1px solid v.$border-color-2;
        color: v.$white;
        padding: 1rem;
        font-size: 1.2rem;
        box-sizing: border-box;

        &:focus{
            border: none;
            border-bottom: 1px solid v.$white-80;
            transition: all ease 0.5s;
            background-color: rgba(0,0,0,0.1);
        }
    }
    button{
        width:20rem;
        height: 4rem;
        border: none;
        outline: none;
        margin-top: 20px;
    }
}

.map{
	filter: grayscale(0.2);
	box-shadow: v.$shadow;
}

//Responsive
@include m.desktopXL {
    .map{
		display: none;
	}
}
@include m.phone {
    .social{
		display: none;
	}
}
