/*=============================================
=            Google Fonts           =
=============================================*/
$Quicksand: 'Quicksand', sans-serif;
$AppleSystem: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

/*=============================================
=            Theme Dark           =
=============================================*/
$main-color: #C19D60;
$secondary-color:#c0aa83;
$body-bg-color: #191919;
$white-80: hsl(0, 0%, 80%);
$white: hsl(0, 0%, 100%);
$shadow: 0 0 1rem hsla(0, 0%, 0%, 0.08);
$black: hsl(0, 0%, 0%);
$select-box-bg-color: hsl(240, 8%, 21%);
$border-color-2: hsl(240, 9%, 25%);
$border-color-1: hsl(240, 9%, 30%);

$yellow-light: hsl(44, 95%, 83%);
$green-light: hsl(158, 68%, 75%);
$red-light: hsl(0, 84%, 80%);


/*=============================================
=            Media Query Sizes          =
=============================================*/
$phone: 480px;
$tablet: 768px;
$desktop: 992px;
$desktopXL: 1200px;