@use 'base/var' as v;
@use 'base/mixins' as m;
.webp .header {
    position: relative;
    background-image: url(../img/banner.webp);

}
.no-webp .header {
    position: relative;
    background-image: url(../img/banner.jpg);
}
.webp .header::before,
.no-webp .header::before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0,0,0,0.5);
}
.header {

    height: 60rem;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
}

.header-section {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid v.$border-color-2;
    padding: 0 3rem;
    transition: 0.6s;
    z-index: 99999;

    .header-logo a {
        font-family: v.$Quicksand;
        font-size: 2.4rem;
        font-weight: 400;
        color: v.$white-80;
    }
    .header-logo span {
        color: v.$main-color;
        font-weight: 700;
    }

    &.sticky {
        padding: 0 5rem;
        background: v.$white;
        & .menu-item a,
        .header-logo a {
            color: v.$black;
        }
        @include m.tablet {
            & .menu-item a {
            color: v.$white;
        }
        }
        
        & .header-hamburger-btn span,
          .header-hamburger-btn span::before,
          .header-hamburger-btn span::after {

            background-color: v.$black;
        }
    }
}

.header .menu-item {
    display: inline-block;
    margin-left: 2.5rem;
    position: relative;
    line-height: 1.2;

    &:hover > a {
        color: v.$main-color;
    }

      a {
        font-family: v.$AppleSystem;
        font-weight: 400;
        font-size: 1.6rem;
        display: block;
        padding: 1.8rem 0;
        color: v.$white;
        text-transform: uppercase;
        transition: color 0.3s ease;
        -webkit-transition: color 0.3s ease;
        -moz-transition: color 0.3s ease;
        -ms-transition: color 0.3s ease;
        -o-transition: color 0.3s ease;
    }
}

.header-close-btn,
.header-hamburger-btn {
    display: none;
}
.header-text {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: v.$white;
    text-align: center;
    height: 100vh;

    h2 {
        font-size: 4rem;
        margin: 0;
    }
    p {
        color: v.$white;
        font-size: 2rem;
        margin: 0;
    }
}
// Responsive
@include m.tablet {
    .header {
        padding: 1.2rem 0;

        .menu-item {
            display: block;
            margin: 0;

            & > a {
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 1.2rem 1.5rem;
                border-bottom: 1px solid v.$border-color-1;
            }

            &:first-child > a {
                border-top: 1px solid v.$border-color-1;
            }
            &.active > a i {
                transform: rotate(-180deg);
                -webkit-transform: rotate(-180deg);
                -moz-transform: rotate(-180deg);
                -ms-transform: rotate(-180deg);
                -o-transform: rotate(-180deg);
        }
        }
    }
    .header.backdrop,
    .header-close-btn,
    .header-hamburger-btn {
        display: block;
    }

    .header-hamburger-btn {
        height: 3.4rem;
        width: 4rem;
        cursor: pointer;
        border: 0;
        background-color: transparent;

        span {
            display: block;
            height: .1rem;
            width: 2.8rem;
            background-color: v.$white;
            position: relative;
            margin: auto;

            &::before,
            &::after {
                content: '';
                position: absolute;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: v.$white;
            }
            &::before {
                top: -.7rem;
                width: 1.5rem;
            }
            &::after {
                top: .7rem;
                left: 1.3rem;
                width: 1.5rem;
            }
        }
    }
    .header-close-btn {
        height: 4rem;
        width: 4rem;
        background-color: v.$main-color;
        position: absolute;
        left: 1.5rem;
        top: 1.5rem;
        color: v.$black;
        border: 0;
        border-radius: .5rem;
        -webkit-border-radius: .5rem;
        -moz-border-radius: .5rem;
        -ms-border-radius: .5rem;
        -o-border-radius: .5rem;
        cursor: pointer;
}
.header-menu {
    position: fixed;
    width: 30rem;
    height: 100vh;
    top: 0;
    right: -30rem;
    z-index: 1000;
    padding: 7rem 0 5rem;
    transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -ms-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    background-color: v.$black;
    box-shadow: 0 0 1rem rgb(255 255 255 / 8%);
    overflow-y: auto;
    visibility: hidden;
    &.open {
        right: 0;
        visibility: visible
    }
}
.header-backdrop {
    position: fixed;
    z-index: 999;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    background-color: v.$black;
    transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -ms-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    visibility: hidden;

    &.active {
        visibility: visible;
        opacity: 0.8;
    }
}
.header-text {

    h2 {
        font-size: 2rem;
    }
    p {
        font-size: 1.2rem;
    }
}
}