@use 'base/var' as v;
@use 'base/mixins' as m;

.site-footer {
    width: 100%;
    background: rgba(0, 0, 0, .8);

    &::before {
        height: .5rem;
        content: "";
        display: block;
        border-top: 1px solid v.$border-color-1;
        box-sizing: border-box;
    }
}
.footer-social {
    padding: 1.5rem;

    display: flex;
    justify-content: center;
    align-items: center;

    a {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 4rem;
        background-color: #0b0b0b;
        margin: .3rem;
        padding: 1rem;
        color: v.$white;
        border-radius: .3rem;
        -webkit-border-radius: .3rem;
        -moz-border-radius: .3rem;
        -ms-border-radius: .3rem;
        -o-border-radius: .3rem;
        transition: all .3s ease;
        -webkit-transition: all .3s ease;
        -moz-transition: all .3s ease;
        -ms-transition: all .3s ease;
        -o-transition: all .3s ease;

        &:hover {
            background-color: v.$main-color;
        }

    }
    i {
        display: flex;
        align-items: center;
    }
}
.copyright {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 1rem;

    a {
        border-bottom: 2px solid v.$main-color;
        color: v.$white-80;
        font-family: v.$Quicksand;
        font-size: 1.3rem;
        font-weight: 900;
        transition: all .4s ease;
        -webkit-transition: all .4s ease;
        -moz-transition: all .4s ease;
        -ms-transition: all .4s ease;
        -o-transition: all .4s ease;

        &:hover {
            background-color: v.$main-color;
        }
    }
    p {
        font-family: v.$AppleSystem;
        font-size: 1.2rem;
        font-weight: 300;
        color: v.$white;
        line-height: 1.6;
        margin: 0;
    }

    img {
        width: 1.5rem;
        height: 1.5rem;
        animation: heart 2s infinite ease;
        -webkit-animation: heart 2s infinite ease;
        margin: 0 .5rem;
    }
    
    @keyframes heart {
        from {
            transform: scale(1)
        }
    
        10% {
            transform: scale(1.2)
        }
    }
}