@use 'base/var' as v;
@use 'base/mixins' as m;
.widget-course {
    border-bottom: 1px solid v.$border-color-2;
    padding-bottom: 1rem;
    margin-bottom: 2rem;
    display: flex;
    justify-content: space-evenly;
    

    .widget-img {
        position: relative;
        flex-basis: 40%;
        box-shadow: 0px 0px 4px 0px v.$white-80;  
        line-height: .5;


        &::before {
            display: block;
            content: '$30';
            position: absolute;
            z-index: 2;
            top: 0px;
            left: 0px;
            text-align: center;
            font-size: 14px;
            font-weight: 600;
            line-height: 26px;
            background: hsl(44, 95%, 83%);
            color: #000;
            padding: 0 1rem;
        }
    }
}
.inner-widget {
    flex-basis: calc(60% - 3rem);

    h4 {
        color: v.$secondary-color;
        font-size: 14px;
        line-height: 1.4;
        margin: 0;
    }
    p {
        display: block;
        font-size: 1.4rem;
        color: v.$white;
        margin: 0;
    }

    .empty {
        color: v.$white;
        margin-left: .5rem;
    }
    .level {
        font-family: v.$Quicksand;
        text-transform: uppercase;
        font-weight: 700;
        border-radius: 0.3rem;
        -webkit-border-radius: 0.3rem;
        -moz-border-radius: .3rem;
        -ms-border-radius: .3rem;
        -o-border-radius: .3rem;
        color: v.$black;
        font-size: 1rem;
        padding: 0.3rem;
        margin-left: .5rem;
    }
    .beginner {
        background-color: v.$green-light;
    }
    
    .middle {
        background-color: v.$yellow-light;
    }
    
    .hard {
        background-color: v.$red-light;
    }
}

.newsletter {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    h3 {
        color: v.$white;
        font-size: 1.6rem;
        line-height: 1;
        margin: 0;
    }
    p {
        font-size: 1.2rem;
        color: v.$white-80;
        margin: 1.2rem 0 0;
        text-align: center;
        line-height: 1.5;
    }
}
.newsletter-form {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: .5rem;

     input,
     button {
    font-family: v.$Quicksand;
    font-weight: 900;
    font-size: 1.2rem;
    background-color: rgba(255, 255, 255, .07);
    line-height: 2.2rem;
    margin: 0;
    border: none;
    padding: 1rem 2.5rem;
    outline: none;
}
     input {
        color: v.$white;
        border-radius: 100px 0 0 100px !important;
    }
     button {
        border-radius: 0 100px 100px 0;
        cursor: pointer;

        i {
            display: flex;
            justify-content: center;
            align-items: center;
            color: v.$white;
            font-size: 2.2rem;
            transition: color .4s ease;
            -webkit-transition: color .4s ease;
            -moz-transition: color .4s ease;
            -ms-transition: color .4s ease;
            -o-transition: color .4s ease;

            &:hover {
                color: v.$secondary-color;
            }
        }
    }
}
.coffee {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    p {
        font-size: 1.2rem;
        color: #f2f2f2;
        margin: 1.2rem 0 0;
        text-align: center;
        line-height: 1.5;
        border-bottom: 1px solid v.$border-color-2;
        margin-bottom: 2rem;
    }
}
