@use 'var' as v;
html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*,
*::before,
*::after {
    box-sizing: inherit;
}
body {
    background-color: v.$body-bg-color;
    font-family: v.$AppleSystem;
    font-size: 1.6rem;
    line-height: 2;

    &.body-scroll {
        padding-top: 5.8rem;
    }
}
.container {
    width: 100%;
    max-width: 120rem;
    margin: 0 auto;
}
.overflow-hidden {
    overflow: hidden!important;
}
.text-center {
    text-align: center;
}
.mt-3 {
    margin-top: 3rem;
}
p {
    font-size: v.$AppleSystem;
    font-size: 2rem;
    color: v.$white-80;
    line-height: 1.5;
}
a {
    color: v.$main-color;
    text-decoration: none;
}
a:hover {
    color: v.$main-color;
}
img {
    max-width: 100%;
}
ul {
    list-style: none;
    margin: 0;
    padding: 0;
}
h1,h2,h3,h4,h5,h6 {
    font-family: v.$Quicksand;
    color: v.$white;
    line-height: 1.3;
}
h3 {
    font-size: 2rem;
}
.title {
    position: relative;
    font-size: 2rem;
    font-weight: 700;
    padding: 0 0 0 1rem;
    white-space: normal;
    display: inline-block;

    &::before {
        position: absolute;
        z-index: 1;
        top: -5%;
        right: auto;
        bottom: -5%;
        left: 0;
        display: block;
        width: 40px;
        height: 110%;
        content: '';
        opacity: .2;
        background-color: transparent;
        background-image: radial-gradient(currentColor 1px, transparent 1px);
        background-position: 1px 1px;
        background-size: 5px 5px;
    }
}
.btn {
    display: inline-block;
    font-family: v.$Quicksand;
    color: v.$white;
    padding: 0.5rem 1.5rem;
    font-size: 1.3rem;
    text-transform: uppercase;
    font-weight: 700;
    border-radius: 2rem .8rem 2rem .5rem;
    -webkit-border-radius: 2rem .8rem 2rem .5rem;
    -moz-border-radius: 2rem .8rem 2rem .5rem;
    -ms-border-radius: 2rem .8rem 2rem .5rem;
    -o-border-radius: 2rem .8rem 2rem .5rem;
    transition: all 0.3s ease-in;
    -webkit-transition: all 0.3s ease-in;
    -moz-transition: all 0.3s ease-in;
    -ms-transition: all 0.3s ease-in;
    -o-transition: all 0.3s ease-in;

    &:hover {
        cursor: pointer;
        border-radius: .5rem 2rem .8rem 2rem;
        -webkit-border-radius: .5rem 2rem .8rem 2rem;
        -moz-border-radius: .5rem 2rem .8rem 2rem;
        -ms-border-radius: .5rem 2rem .8rem 2rem;
        -o-border-radius: .5rem 2rem .8rem 2rem;
    }
}
.btn-secondary {
    background-color: v.$main-color;
}
.box {
    background: rgba(0, 0, 0, .07);
    margin: 0 0 3.5rem;
    padding: 3rem 2rem;
}