@use 'base/var' as v;
@use 'base/mixins' as m;

.courses-section {
    padding: 0 2rem;
    margin: 2rem 0;
    @include m.grid(3, 2rem);

}

.course {
    position: relative;
    background-color: v.$body-bg-color;
    transition: .4s ease all;
    margin-bottom: 1rem;
    box-shadow: v.$shadow;
    cursor: pointer;

    img {
        width: 100%;
        height: auto;
    }
}

.inner-course {
    padding: 0 1rem;

    .level {
        position: absolute;
        top: -1rem;
        left: 50%;
        display: inline-block;
        text-transform: uppercase;
        font-size: 10px;
        font-weight: 700;
        min-width: 40px;
        text-align: center;
        padding: 2px 6px;
        border-radius: 3px;
        background-color: v.$yellow-light;
        color: v.$black;
        letter-spacing: 0.05em;
        transform: translate(-50%, 0);
    }
    .beginner {
        background-color: v.$green-light;
    }
    
    .middle {
        background-color: v.$yellow-light;
    }
    
    .hard {
        background-color: v.$red-light;
    }

    h2 {
        color: v.$main-color;
        font-size: 1.8rem;
        font-family: v.$Quicksand;
        margin: 1rem 0;
        text-align: center;
        line-height: 1.2;
        font-weight: 700;
    }

    p {
        color: v.$white-80;
        padding: 0 0 1rem 0;
        font-size: 1.2rem;
        border-bottom: 1px solid v.$border-color-2;
    }
}

.course-footer {
    display: flex;
    justify-content: space-between;
    line-height: 3;
    padding: 0 1rem;
    align-items: center;
    p {
        display: block;
        font-size: 1.4rem;
        color: v.$white;
        margin: 0;
    }
    .empty {
        color: v.$white;
        margin-left: .5rem;
    }
}
.price {
    .before,
    .after {
        font-weight: 500;
    font-size: 1.6rem;
    color: v.$white;
    }
    .before {
        font-size: 1.4rem;
        color: #808080;
        text-decoration: line-through;
        margin-left: 1rem;
    }
    .after {
        font-weight: bold;
    }
}


// Responsive
@include m.tablet {
    .grid-column{
        grid-column: 1 / 3;
    }
    .courses-section {
        @include m.grid(2, 2rem);
    }
    
}
@include m.phone {
    .courses-section {
        @include m.grid(1, 2rem);
    }
    .grid-column{
        grid-column: auto;
    }
}