@use 'base/var' as v;
@use 'base/mixins' as m;

.about-section{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: relative;
    background: url(../img/about.jpg) no-repeat left;
    background-size: contain;
    background-color: v.$body-bg-color;
    overflow: hidden;
    padding: 8rem 0;
}

.about-container{
    position: relative;
    width: 55%;
    padding: 8rem;
    background-color: v.$body-bg-color;
    box-shadow: 230px 1px 9px 0px #0000007a;

    h2 {
        margin-bottom: 2rem;
        font-size: 3rem;
        font-weight: 900;
    }

    p {
        font-size: 1.4rem;
        color: v.$white-80;
        line-height: 1.5;
    }
}
.counters {
    text-align: center;
    margin-top: 5rem;
    @include m.grid(3, 2rem);


    .item {
        display: flex;
        flex-direction: column;
        color: v.$secondary-color;
         i {
            font-size: 2.5rem;
        }
        .counter {
            margin-top: 1rem;
            font-size: 1.6rem;
            font-weight: 700;
            
            &::before {
                content: '+';
                width: 1rem;
                height: 1rem;
            }
        }
        .counter-text {
            font-family: v.$Quicksand;
            text-transform: uppercase;
            font-weight: 700;
            font-size: 1.3rem;
            color: v.$white-80;
            line-height: 1.2;
        }
    }
}
// Responsive
@include m.desktopXL {
    .about-container{
        padding: 8rem;
    }
}
@include m.desktop {
    .about-section{
        background-size: cover;
        padding: 10rem 4rem;
    }
    .about-container{
        width: 100%;
    }
}

@include m.tablet {
    .about-section{
        padding: 0;
    }
    .about-container{
        padding: 6rem;
    }
    .counters {

        @include m.grid(3, 1rem);

        .counter {
            font-size: 5rem;
        }
    }
}
