@use 'var' as v;

@mixin phone {
    @media (max-width: v.$phone) {
        @content;
    }
}

@mixin tablet {
    @media (max-width: v.$tablet) {
        @content;
    }
}

@mixin desktop {
    @media (max-width: v.$desktop) {
        @content;
    }
}
@mixin desktopXL {
    @media (max-width: v.$desktopXL) {
        @content;
    }
}

@mixin grid($columns, $spacing) {
    display: grid;
    grid-template-columns: repeat($columns, minmax(auto, 1fr));
    gap: $spacing;
}
